@-webkit-keyframes Gradient {
    0% {
        background-position: 0% 50%
    }
    50% {
        background-position: 100% 50%
    }
    100% {
        background-position: 0% 50%
    }
}

@-moz-keyframes Gradient {
    0% {
        background-position: 0% 50%
    }
    50% {
        background-position: 100% 50%
    }
    100% {
        background-position: 0% 50%
    }
}

@keyframes Gradient {
    0% {
        background-position: 0% 50%
    }
    50% {
        background-position: 100% 50%
    }
    100% {
        background-position: 0% 50%
    }
}



.facebookLoginBtn {
    background: transparent !important;
    border: solid 2px #fff;
    color: #fff;
    font-weight: '800';
    border-radius: 25px;
    padding-top: 6px;
    /* padding-bottom: 5px; */
    padding-right: 20px;
    align-items: center;
    font-weight: 400;
}

.facebookLoginBtn > i {
    padding: 5px 10px 5px 10px;
  /*  font-size: initial;*/
}

.loginContainer {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 80%;
    bottom: 10%;
}


.LoadingVar {
            background: linear-gradient(to right, #ffffff 10%, rgba(58, 207, 213, 0) 80%) !important;
            background-size: 400% 400% !important;
            -webkit-animation: Gradient 2s ease infinite;
            -moz-animation: Gradient 2s ease infinite;
}